*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;

}

body{
  font-family: 'Montserrat',sans-serif;
}

.banner{
  width: 100%;
  height: 100vh;
  background: url('img/temp.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  position:relative;
}

.banner_container {
  display: flex;
  align-items: center;
  height: 100vh;
  color:#fff;
}

.container{
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
 
}

.banner_text {
  width: 500px;
}

.banner_text h3 {
  font-size: 50px;
}

.banner_text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 800;
  margin-top: 20px;
}

.banner_text p {
  font-size: 25px;
  font-weight: 400;
  margin-top: 20px;
}



.navbar {
  top:0;
  left: 0;
  width: 100%;
  height: 60px;
}

.logo {
  width: 40%;
  float: left;
  padding-left: 10%;
}

.logo img {
  width: 200px;
  margin-top: 10px;
}

.links ul li a {
  text-decoration: none;
  color: #fff;
  font-weight: 800;
  padding-left: 30px;
}

.links ul li a:hover {
  text-decoration: none;
  color: #888888;
  font-weight: 800;
  padding-left: 26px;
}


.links ul li{
  float: left;
  list-style-type: none;
  margin-top: 10px;
}

.links {
  width: 60%;
  float: right;
  justify-content: flex-end;

  
}

.banner_btn{
  margin-top: 30px;
}

.banner_btn a {
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 30px;
  background: rgb(255, 69,79);
}


.about {
  padding: 80px ;
  width: 100%;
  display: flex;
}




.column-6 {
  width:50%;

}
 .column-3{
  width:50%;
 padding: 0px 30px;
 }

 .about__btn a {
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 30px;
  background: rgb(255, 69,79);
}

.about h3{
  color:rgba(37,37,37,0.7);
  font-size: 30px;
  margin-bottom: 12px;
  font-weight: 700;
}

.about h1{
  font-size:40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;
}

.about p {
  font-size: 18px;
  columns: #8b8b8b;
  font-weight:300;
}

.about__btn{
  margin-top: 20px;
  overflow: hidden;
}

.about__img img{
  width: 100%;
  height: 350px;
  box-shadow: 10px 10px;
}




.shop {
  width: 100%;
  background: url("./img/temp.jpg");
  height: 450px;
  background-attachment: fixed;
  
}

.shop__btn a {
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 20px 30px;
  background: rgb(255, 69,79);
}

.shop_text{
  height: 450px;
  display: flex;
  text-align: center;
  align-items: center;
  width: 400px;
  justify-content: center;
  margin: 0 auto;
  color: #fff;
}

.shop_text h1{
  font-size: 40px;
  line-height: 60px;
  font-weight: 600;
  font-style: normal;
  letter-spacing: 0.5px;
}

.shop__btn {
  margin-top: 35px;
}



.clients {
  width: 100%;
  padding: 80px 0;
  background: url("./img/temp.jpg");
  background-repeat: no-repeat;
  color: #fff;
  background-attachment: fixed;
}
.col-3{
  width: 25%;
}

.client {
  text-align: center;
}

.client h1{
  font-style: 45px;
}

.client p {
  font-family: 18px;
  font-weight: 400;
  margin-top: 10px;
}


.prices {
  width: 100%;
  padding-bottom:30px;
}

.price:hover{
  opacity: 0.9;
  color:rgb(234, 71, 80);
}


.price{
  box-shadow: 10px 10px 5px grey;
  border-radius: 8px;
  color: #fff;
  margin: 10px 0px 30px 0px;
  padding:10px 10px;
  background-color: rgb(54, 52, 52);
}


.price__img img{
  width: 100%;
  height: auto;
}


.price__heading {
  margin-top: 14px;
  font-size: 25px;
}

.price__text {
  margin-top: 10px;
  color: whitesmoke;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-align: justify;
}


.price__btn a{
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px; 
  padding: 17px 30px;
  background: rgb(255, 69,79);
}

.price__btn{
  margin-bottom: 10px;
  overflow: hidden;
}

.contents{
  width: 100%;
  height: 115vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-image: url("./img/download.jpg");
  font-weight: 800;
  color: #fff;
  font-size: 20px;
}

.contents h1{
  font-weight: 800;
 font-size: 70px;
 color:white;
}


.contents input{
  border-radius:10px;
  height:40px;
  opacity: 0.8;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.contents button{
  background-color:white;
  border-radius: 10px;
  margin-top: 20px;
  opacity: 0.8;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}



.roll h1{
  text-align: center;
  font-size: 80px;
  font-weight: 700;
}
.roll h3{
  text-align: center;
  font-size: 20px;
  color: #888888;
  font-weight: 500;
}

.pics{
width:90%;
margin:50px 5%;
height: 500px;
box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.picleft{
  width: 55%;
  float: left;
  height:500px;
  padding:0px 6px;
}

.picleft img{
  width:100% ;
  height: 100%;
}

.picright{
  width: 45%;
  float: left;
  padding: 0px 5px ;
  height:100%;
}


.picright img{
  width:100%; 
  height:50%
}

.footer{
  left:0;
  margin-top: 250px;
 height: 300px;
  bottom: 0;
  width: 100%;
  background-image: url("./img/temp.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}


.foot-left{
  width: 40%;
  float: left;
  padding: 0px 50px;
}

.foot-left p{
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  text-align: justify;
}

.foot-left h3{
  font-size: 30px;
  color: #fff;
 font-weight: 800;
}

.foot-left img{
  width: 200px;
  padding-top:20px; 
  padding-bottom:20px
}

.foot-right{
  width: 60%;
  float: right;
  padding-top:25px;
}

.flink{
  width: 50%;
  height: 100%;
  float: left;
}

.flink h3{
  color: #fff;
  font-size: 30px;
  font-weight: 800;
}

#town{
  color: rgb(234, 71, 80);
}

.flink ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
}

.flink ul li a:hover {
  text-decoration: none;
  color: #888888;
  font-weight: 800;
}


.flink ul li{
  list-style-type: none;
  margin-top: 10px;
}

.add{
  float: left;
  width: 50%;
  height: 100%;
}

.add p{
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  text-align: justify;
}

.add h3{
  color: #fff;
  font-size: 30px;
  font-weight: 800;
}

